@import 'styles/vars';
@import 'styles/mixins';
@import 'styles/fonts';


.task-item {
    background: $white;
    border-radius: $main-br;
    box-shadow: $secondary-shadow;
    max-width: 100%;
    width: 100%;
    position: relative;
    // border-bottom: 4px solid $white;

    &-container {
        width: 100%;
    }
    &__header, &__main, &__footer {
        padding: 4px 10px;
    }

    &__header {
        @include flex($gap: 6px);
        background: $orange;
        border-radius: $main-br;
        max-width: 100%;
        height: auto;
    }

    &__title {
        font-size: $size-s;
        max-width: 50%;
    }

    &__handle {
        width: 100%;
        height: 100%;
        cursor: grab;
        position: absolute;
        top: 0;
        left: 0;
    }
    &__control {
        width: 30px;
        cursor: pointer;
        transition: $transition-main;
        &:hover {
            
            filter: $filter-white;
        }
    }

    &__main {
        padding: 7px 10px 0 10px;
        @include flex($direction: column, $align: stretch, $gap: 6px)
    }

    &__desc {
        font-size: $size-xxs;
        font-weight: 700;
    }

    &__footer {
        @include flex();
        // border-top: 1px solid $light-gray;
    }

    &__date, &__info {
        color: $gray;
        font-size: $size-xxxs;
    }

    &__date {
        font-size: $size-xxxxs;
        @include flex();
        color: $gray;
        cursor: pointer;

        opacity: 0.7;
        transition: $transition-main;

        &:hover {
            opacity: 1;
        }
        gap: 8px;
        &-container {
            @include flex($gap: 10px, $align: end)

        }

        &_ico {
        width: 17px;
        height: 17px;
        
        }


    }
    &__info {
        @include flex($gap: 10px);
    }

    &__notes, &__comments {
        cursor: pointer;
        transition: $transition-main;
        width: 17px;
        height: 17px;
        color: $gray;
        opacity: 0.7;
        &:hover {
            opacity: 1;
        }
    }

    


}


@include media-max-m {
    .task-item {
       
        
        
       
    }
}

@include media-max-s {
    .task-item {
        &__title {
            font-size: $size-xxxxs;
        }

        &__header {
            padding: 8px 6px;
        }
        
        &__control{
            width: 16px;
            height: 16px;
        }

        &__main {
            padding: 10px 6px 0 6px;
            gap: 9px;
        }

        &__footer{
            padding: 6px;
        }
        
        &__desc {
            
            font-size: $size-xxxxxs;
            font-weight: 700;
        }
        
        &__notes, &__date-img {
            width: 14px;
            height: 14px;
        }

        &__comments {
            width: 16px;
            height: 16px;
        }
       
    }
}