@import 'styles/vars';
@import 'styles/mixins';
@import 'styles/fonts';


.comment-modal {
    @include flex($direction: column, $gap: 30px, $align: stretch, $justify: end);
    padding: 20px 5px 5px 5px;
    width: 800px;
    height: 500px;
    max-width: 100%;
    max-height: 100%;
    &__content {
        width: 100%;

        @include flex($direction: column, $justify: start, $gap: 10px, $align: stretch);
        overflow-y: auto;
        overflow-x: hidden;
        padding-right:8px;
        
        &::-webkit-scrollbar {
            width: 8px;
            margin-left: 10px;
           
        }
        &::-webkit-scrollbar-thumb {
            background: $gray;
            width: 5px;
            border-radius: 999px;
        }
        &::-webkit-scrollbar {
            
        }
        &::-webkit-scrollbar {
            
        }
    }

    &__form {
        @include flex( $justify: stretch, $gap: 15px);
        max-height: 20%;
        width: 100%;
        max-width: 100%;
        max-height: 100%;
    
        &__input {
            @include input;
            width: 100%;
            
        }
    
        &__button {
            @include button-primary();
            display: flex;
        }
    
        &__elem {
            @include flex($direction: column, $justify: stretch, $gap: 10px, $align: stretch);
        }
    }
}